.StatusMessage {
  height: 55px;
  text-align: center;
  padding: 7px 20px;
  width: 650px;
  margin: 0 auto;
  background: #F7F8F9; }
  .StatusMessage_complete {
    background: none;
    padding: 12px 20px; }
  .StatusMessage_wrapper {
    display: inline-flex;
    align-items: flex-start;
    margin: 0 auto; }
  .StatusMessage img {
    display: block;
    float: left;
    width: 30px;
    margin-right: 15px; }
  .StatusMessage_message {
    font-weight: bold;
    font-size: 18px;
    line-height: 1;
    padding-top: 6px; }
  .StatusMessage_complete .StatusMessage_message {
    font-weight: normal;
    font-size: 14px;
    line-height: 30px;
    padding-top: 0; }
  .StatusMessage_estimate {
    font-size: 0.75rem;
    color: #747E8D;
    padding-top: 2px; }
