.SwapCompleted {
  position: relative;
  top: -10px; }
  .SwapCompleted .SwapPairPanel {
    z-index: 3; }
  .SwapCompleted_top {
    position: absolute;
    width: 100%;
    top: 80px;
    padding: 80px 0 28px 0;
    box-shadow: 0px 2px 14px rgba(69, 73, 91, 0.04);
    border-radius: 0 0 22px 22px;
    background: #FFFFFF;
    z-index: 2; }
  .SwapCompleted_handshake {
    width: 70px;
    height: 70px;
    display: inline-block;
    position: absolute;
    background: #FFFFFF;
    bottom: -35px;
    right: -35px;
    left: -35px;
    border-radius: 50%;
    margin: auto;
    z-index: 2; }
    .SwapCompleted_handshake img {
      width: 38px;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto; }
  .SwapCompleted_label {
    width: 100%;
    position: absolute;
    top: 80px;
    text-align: center;
    color: #9D4DFA; }
  .SwapCompleted_subLabel {
    width: 100%;
    position: absolute;
    top: 115px;
    text-align: center;
    color: #9D4DFA;
    z-index: 3; }
  .SwapCompleted_bottom {
    position: relative;
    text-align: center;
    margin-top: 100px;
    padding: 80px 30px 40px 30px;
    background: #F7F8F9;
    box-shadow: 0px -2px 14px rgba(69, 73, 91, 0.04);
    border-radius: 0 0 22px 22px;
    z-index: 1; }

.twitter-share-button {
  display: block;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto; }
