.SwapOfferSelection {
  position: relative; }
  .SwapOfferSelection .SwapPairPanel {
    z-index: 3; }
  .SwapOfferSelection_assetSelector {
    padding-top: 20px;
    box-shadow: 0px 2px 14px rgba(69, 73, 91, 0.04);
    border-radius: 0 0 22px 22px;
    background: #FFFFFF;
    z-index: 2; }
  .SwapOfferSelection_top {
    position: relative;
    width: 100%;
    padding-bottom: 28px;
    box-shadow: 0px 2px 14px rgba(69, 73, 91, 0.04);
    border-radius: 0 0 22px 22px;
    background: #FFFFFF;
    z-index: 2; }
  .SwapOfferSelection_bottom {
    position: relative;
    top: -40px;
    text-align: center;
    padding: 160px 30px 20px;
    background: #F7F8F9;
    box-shadow: 0px -2px 14px rgba(69, 73, 91, 0.04);
    border-radius: 22px;
    z-index: 1; }
  .SwapOfferSelection_host {
    position: absolute;
    width: 160px;
    height: 160px;
    background: #FFFFFF;
    left: 0;
    right: 0;
    margin: 0 auto;
    bottom: -70px;
    border-radius: 50%;
    text-align: center;
    padding-top: 40px; }
    .SwapOfferSelection_host img {
      height: 52px;
      margin: 10px auto 0 auto; }
  .SwapOfferSelection_errorMessage {
    height: 0.75rem;
    color: #F12274;
    font-size: 0.75rem;
    line-height: 0.75rem;
    padding: 2px 0;
    box-sizing: content-box;
    text-align: center; }
