.CurrencyInput {
  width: 270px; }
  .CurrencyInput input::-webkit-outer-spin-button,
  .CurrencyInput input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }
  .CurrencyInput input[type='number'] {
    -moz-appearance: textfield; }
  .CurrencyInput_heading {
    display: block;
    text-align: center;
    margin-bottom: 0; }
  .CurrencyInput_inputWrapper {
    border-bottom: 1px solid #31E4C3;
    padding-bottom: 1px; }
    .CurrencyInput_inputWrapper.disabled {
      border: none; }
  .CurrencyInput_input {
    border: 0;
    outline: 0;
    background: none;
    display: block;
    width: 270px;
    height: 32px;
    text-align: center;
    font-size: 30px;
    font-weight: 500; }
    .CurrencyInput_input.error {
      border: 1px solid #F12274; }
  .CurrencyInput_price {
    text-align: center;
    font-size: 0.75rem;
    color: #747E8D;
    padding-top: 3px; }
  .CurrencyInput_label {
    height: 20px;
    padding-top: 1px;
    font-size: 0.75rem;
    text-align: center;
    color: #747E8D; }
  .CurrencyInput_errorMessage {
    height: 0.75rem;
    color: #F12274;
    font-size: 0.75rem;
    line-height: 0.75rem;
    padding: 2px 0;
    box-sizing: content-box;
    text-align: center; }
