.SwapOfferConfirmation .TimeProgressBar {
  margin: 20px; }

.SwapOfferConfirmation_terms {
  padding-top: 30px;
  text-align: center;
  font-weight: bold;
  font-size: 20px; }

.SwapOfferConfirmation_rate {
  margin-top: 10px;
  text-align: center;
  font-weight: bold;
  font-size: 1rem;
  color: #747E8D; }

.SwapOfferConfirmation_info {
  width: 630px;
  margin: 0 auto;
  border-top: 1px solid #bababa;
  text-align: center;
  margin-top: 50px;
  padding-top: 50px; }
  .SwapOfferConfirmation_info p:nth-child(1) {
    width: 280px;
    text-align: left;
    margin: 0 auto; }
  .SwapOfferConfirmation_info ul {
    display: inline-block;
    list-style-position: inside;
    padding-left: 0; }
    .SwapOfferConfirmation_info ul li {
      text-align: left; }

.SwapOfferConfirmation_bottom {
  text-align: center;
  padding-top: 40px;
  min-height: 210px;
  border-radius: 0 0 7px 7px; }

.SwapOfferConfirmation_errorMessage {
  height: 0.75rem;
  color: #F12274;
  font-size: 0.75rem;
  line-height: 0.75rem;
  padding: 2px 0;
  box-sizing: content-box;
  text-align: center; }
