.ExpirationDetails {
  text-align: left;
  display: flex;
  padding: 14px 30px;
  height: 90px;
  margin: 0 auto;
  background: #FFFFFF;
  box-shadow: 0px 2px 14px rgba(69, 73, 91, 0.04);
  border-radius: 22px; }
  .ExpirationDetails_passed, .ExpirationDetails_left {
    display: flex;
    align-items: center; }
  .ExpirationDetails_passed {
    margin-right: 20px; }
  .ExpirationDetails_left {
    margin-left: 20px; }
  .ExpirationDetails_clock {
    width: 25px;
    margin-right: 20px; }
  .ExpirationDetails_center {
    flex: 1;
    display: flex;
    flex-direction: column; }
  .ExpirationDetails_top, .ExpirationDetails_bottom {
    display: flex;
    justify-content: space-between; }
  .ExpirationDetails_terms {
    font-weight: bold; }
    .ExpirationDetails_terms em {
      font-style: normal;
      color: #9D4DFA; }
  .ExpirationDetails_link {
    cursor: pointer; }
    .ExpirationDetails_link img {
      margin-left: 10px; }
  .ExpirationDetails_transactions {
    font-size: 11px; }
  .ExpirationDetails_transaction {
    float: left;
    width: 50%; }
    .ExpirationDetails_transaction:nth-child(2) {
      text-align: right; }
    .ExpirationDetails_transaction_name {
      padding-right: 10px; }
    .ExpirationDetails_transaction_link {
      padding-right: 10px; }
    .ExpirationDetails_transaction_confirmations {
      color: #747E8D; }
    .ExpirationDetails_transaction_missing {
      display: inline-block;
      width: 160px;
      color: #9D4DFA;
      text-align: left; }
  .ExpirationDetails_progress {
    background: #D8E2F0;
    height: 3px;
    margin: 10px 0; }
    .ExpirationDetails_progress_fill {
      background: #1CE5C3;
      height: 3px; }
