.WalletDisplay {
  text-align: center;
  width: 160px;
  min-height: 126px; }
  .WalletDisplay .Button {
    margin-top: 2px; }
  .WalletDisplay.error > .WalletDisplay_wrapper {
    padding: 10px 0;
    border: 1px solid #F12274; }
  .WalletDisplay p {
    margin-bottom: 2px; }
  .WalletDisplay_icon {
    height: 20px;
    margin-bottom: 6px; }
  .WalletDisplay_address {
    width: 160px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    font-size: 0.75rem;
    color: #747E8D;
    padding: 0 0 0 4px; }
    .WalletDisplay_address:hover {
      overflow: visible; }
    .WalletDisplay_address.error {
      border: 1px solid #F12274;
      padding: 2px 4px; }
  .WalletDisplay_message {
    font-size: 0.75rem;
    color: #9D4DFA; }
    .WalletDisplay_message.error {
      color: #F12274; }
  .WalletDisplay_balance {
    color: #747E8D; }
    .WalletDisplay_balance.error {
      border: 1px solid #F12274; }
  .WalletDisplay_errorMessage {
    height: 0.75rem;
    color: #F12274;
    font-size: 0.75rem;
    line-height: 0.75rem;
    padding: 2px 0;
    box-sizing: content-box;
    text-align: center; }
