.LiqualitySwap {
  height: 100vh;
  display: flex;
  flex-direction: column; }
  .LiqualitySwap_bar {
    flex: 0 0 6px;
    height: 6px;
    background: #1ce4c3;
    /* Old browsers */
    background: -moz-linear-gradient(left, #1ce4c3 0%, #ac38fd 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(left, #1ce4c3 0%, #ac38fd 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, #1ce4c3 0%, #ac38fd 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1ce4c3', endColorstr='#ac38fd',GradientType=1 );
    /* IE6-9 */ }
  .LiqualitySwap_header {
    background: #FFFFFF;
    padding: 20px; }
  .LiqualitySwap_logo {
    height: 25px; }
  .LiqualitySwap_nav {
    float: right; }
  .LiqualitySwap_main {
    flex: 1;
    padding: 1rem;
    background-image: url(data:image/png;base64,);
    position: relative; }
  .LiqualitySwap_wave {
