.SwapPairPanel {
  position: relative; }
  .SwapPairPanel_assetsBG {
    display: block;
    margin: 0 auto; }
  .SwapPairPanel_icon {
    position: absolute;
    display: block;
    margin: 0 auto;
    top: 123px;
    left: 0;
    right: 0; }
    .SwapPairPanel_icon.disabled {
      filter: gray;
      -webkit-filter: grayscale(100%); }
  .SwapPairPanel_assetContainer {
    position: relative; }
    .SwapPairPanel_assetContainer_main {
      position: relative;
      z-index: 3; }
  .SwapPairPanel_filler {
    position: absolute;
    width: 100%;
    height: 72px;
    background: #ffffff;
    bottom: 0;
    z-index: 1; }
  .SwapPairPanel_asset {
    position: absolute;
    height: 48px;
    margin-top: 50px; }
    .SwapPairPanel_asset_left {
      left: 294px; }
    .SwapPairPanel_asset_right {
      right: 294px; }
    .SwapPairPanel_asset_interactive {
      cursor: pointer; }
      .SwapPairPanel_asset_interactive:hover {
        opacity: 0.7; }
  .SwapPairPanel_labels {
    position: absolute;
    top: 33px;
    width: 100%;
    z-index: 1;
    text-transform: uppercase; }
    .SwapPairPanel_labels > h1 {
      border: 7px solid #fff;
      line-height: normal;
      padding-top: 23px;
      width: 40%;
      height: 89px;
      color: #fff; }
    .SwapPairPanel_labels_have {
      float: left;
      background: #9D4DFA;
      border-radius: 22px 22px 22px 0px;
      padding-left: 80px; }
      .SwapPairPanel_labels_have.SwapPairPanel_labels_have_faded {
        background: #d4b0fd; }
    .SwapPairPanel_labels_want {
      float: right;
      background: #1CE5C3;
      border-radius: 22px 22px 0 22px;
      text-align: right;
      padding-right: 80px; }
      .SwapPairPanel_labels_want.SwapPairPanel_labels_want_faded {
        background: #a5f5e7; }
  .SwapPairPanel_currency_labels {
    width: 100%;
    padding-top: 14px;
    z-index: 1;
    background: #ffffff; }
    .SwapPairPanel_currency_labels > h4 {
      line-height: normal;
      width: 50%;
      text-align: center; }
    .SwapPairPanel_currency_labels_have {
      float: left;
      padding-left: 190px; }
    .SwapPairPanel_currency_labels_want {
      float: right;
      padding-right: 190px; }
    .SwapPairPanel_currency_labels_interactive {
      cursor: pointer;
      color: #9D4DFA; }
      .SwapPairPanel_currency_labels_interactive:hover {
        opacity: 0.7; }
    .SwapPairPanel_currency_labels::after {
      content: "";
      display: block;
      clear: both; }
