.AssetSelector_search {
  padding: 10px 10px 30px 10px;
  background: #F7F8F9;
  border-radius: 7px 7px 0 0; }
  .AssetSelector_search input {
    border: 0;
    outline: 0;
    background: none;
    color: #747E8D;
    width: 350px;
    height: 45px;
    padding: 8px 20px;
    background: #FFFFFF;
    border: 1px solid #747E8D;
    border-radius: 25px;
    font-size: 13px;
    flex: 1; }

.AssetSelector_close {
  float: right;
  width: 45px;
  height: 45px;
  border: 0;
  background: none;
  cursor: pointer; }

.AssetSelector_list {
  position: relative;
  top: -20px;
  margin: 0;
  padding: 25px;
  background: #FFFFFF;
  border-radius: 22px;
  list-style: none; }

.AssetSelector_asset {
  display: inline-block;
  width: 25%;
  text-align: center;
  cursor: pointer; }
  .AssetSelector_asset_name {
    color: #333;
    margin-top: 20px; }
  .AssetSelector_asset_icon {
    width: 90px; }
